/* تنسيقات عامة */
body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 0;
    direction: rtl; /* لجعل النص يبدأ من اليمين ليتناسب مع اللغة العربية */
    background-color: #f4f4f4;
}

a {
    text-decoration: none;
    color: inherit;
}

/* تنسيق الهيدر (Header) */
header {
    background-color: #4CAF50;
    color: white;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header nav a {
    color: white;
    margin: 0 1rem;
    font-weight: bold;
    transition: color 0.3s ease;
}

header nav a:hover {
    color: #a5d6a7;
}

/* تنسيقات صفحة الهبوط (Landing Page) */
.landing-page-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    background: url('https://images.unsplash.com/photo-1519331571438-e6d871d34c1b?crop=entropy&cs=tinysrgb&fit=max&fm=jpg&ixid=M3w1NjU0NDF8MHwxfHNlYXJjaHwxfHxwbGFudHMlMjBib3Rhc3x8MjMyfHx2ZWN0b3J8fDE3MjE4MzY3NzJ8Mg&ixlib=rb-4.0.3&q=80&w=1080') no-repeat center center/cover;
    position: relative;
    padding: 2rem;
}

.landing-page-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.company-name {
    font-size: 3.5rem;
    margin-bottom: 1rem;
    position: relative;
}

.company-description {
    font-size: 1.2rem;
    line-height: 1.6;
    max-width: 600px;
    margin-bottom: 2rem;
    position: relative;
}

.start-button {
    background-color: #4CAF50;
    color: white;
    padding: 1rem 2rem;
    border: none;
    border-radius: 5px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    position: relative;
}

.start-button:hover {
    background-color: #388E3C;
}

/* تنسيقات صفحة قائمة المنتجات (Product List Page) */
.product-list-page-container {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
}

.product-list-page-container h2 {
    text-align: center;
    color: #333;
    margin-bottom: 2rem;
}

.product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 2rem;
}

.product-card {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.product-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 1rem;
}

.product-card h3 {
    margin-top: 0;
    font-size: 1.2rem;
}

.product-card .price {
    font-size: 1.1rem;
    color: #4CAF50;
    font-weight: bold;
}

.product-card button {
    background-color: #FF9800;
    color: white;
    border: none;
    padding: 0.8rem 1.5rem;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 1rem;
}

.product-card button:hover {
    background-color: #FB8C00;
}

/* تنسيقات صفحة عربة التسوق (Cart Page) */
.cart-page-container {
    padding: 2rem;
    max-width: 900px;
    margin: 0 auto;
}

.cart-page-container h2 {
    text-align: center;
    color: #333;
    margin-bottom: 2rem;
}

.cart-items {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
}

.cart-item {
    display: flex;
    align-items: center;
    padding: 1rem 0;
    border-bottom: 1px solid #eee;
}

.cart-item:last-child {
    border-bottom: none;
}

.cart-item .item-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 8px;
    margin-left: 1rem;
}

.cart-item .item-details {
    flex-grow: 1;
}

.cart-item .item-details h3 {
    margin: 0;
    font-size: 1.1rem;
}

.cart-item .item-quantity {
    display: flex;
    align-items: center;
    margin-top: 0.5rem;
}

.cart-item .item-quantity button {
    background-color: #ddd;
    border: none;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    font-size: 1.2rem;
    cursor: pointer;
}

.cart-item .item-quantity span {
    margin: 0 1rem;
}

.cart-item .remove-button {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    cursor: pointer;
}

.cart-summary {
    background-color: #e8f5e9;
    border-radius: 10px;
    padding: 1.5rem;
    margin-top: 2rem;
    text-align: center;
}

.cart-summary h3 {
    margin-top: 0;
    color: #333;
}

.cart-buttons .continue-shopping {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 1rem 2rem;
    border-radius: 5px;
    cursor: pointer;
    margin-left: 1rem;
}

.cart-buttons .checkout-button {
    background-color: #2196F3;
    color: white;
    border: none;
    padding: 1rem 2rem;
    border-radius: 5px;
    cursor: pointer;
}